.wp-block-plz-register {
	min-height: 90vh;
	display: flex;
	align-items: center;
	justify-content: center;

	.signin__container {
		margin: 0 auto;
		display: block;
		max-width: 400px;
		width: 100%;
		margin: 20px 0;

		.sigin__titulo {
			font-family: Quicksand;
			font-style: normal;
			font-weight: bold;
			font-size: 22px;
			line-height: 22px;
			color: #000000;
			margin-bottom: 40px;
		}

		.signin__form {
			.name--campo {
				display: flex;
				flex-direction: column;
				margin-bottom: 20px;

				label {
					font-family: Quicksand;
					font-style: normal;
					font-weight: bold;
					font-size: 14px;
					line-height: 17px;
					color: #000000;
					margin-bottom: 5px;
				}

				input:not([type="submit"]) {
					background: #f7f7f7;
					border: 1px solid #f7f7f7;
					box-sizing: border-box;
					border-radius: 10px;
					height: 45px;
					font-family: Quicksand;
					font-style: normal;
					font-weight: normal;
					font-size: 16px;
					line-height: 20px;
					color: #797979;
					text-indent: 10px;

					&:focus {
						outline: none;
					}
				}
			}

			input[type="submit"] {
				margin-top: 30px;
				background: #acd9b2;
				border: 1px solid #acd9b2;
				box-sizing: border-box;
				box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.0001);
				border-radius: 10px;
				width: 100%;
				height: 55px;
				font-family: Quicksand;
				font-style: normal;
				font-weight: bold;
				font-size: 18px;
				line-height: 22px;
				text-align: center;
				color: #ffffff;
				transition: 200ms;

				&:hover {
					background-color: #000000;
				}

				&:focus {
					outline: none;
				}
			}

			.signin_create-link {
				margin-top: 30px;

				a {
					border: 1px solid #acd9b2;
					box-sizing: border-box;
					filter: drop-shadow(0px 0px 4px rgba(0, 0, 0, 0.0001));
					border-radius: 10px;
					width: 100%;
					height: 55px;
					display: inline-block;
					text-decoration: none;
					color: #acd9b2;
					font-family: Quicksand;
					font-style: normal;
					font-weight: bold;
					font-size: 18px;
					line-height: 55px;
					text-align: center;
					transition: 200ms;

					&:hover {
						background-color: #000;
						color: white;
					}
				}
			}
		}
	}
}
